.ImageView {
  margin: auto;
}

.ImageView .card-img-top, .ImageView video {
  max-height: 800px;
  max-width: 1500px;
}

.ImageView .card-body {
  background-color: #222;
}

.ImageView .card-title {
  color: #f0f0f0;
}




.ImagePreview {
  margin: auto;
  width: 300px;
}

.ImagePreview .card-img-top {
  object-fit: none;
  object-position: center;
  width: 100%;
  height: 250px;
  background-color: #000;
}

.ImagePreview .card-body {
  background-color: #222;
}

.ImagePreview .card-title {
  color: #d0d0d0;
}

.ImagePreview .card-title:hover {
  color: #fff;
}
