body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.ImageView {
  margin: auto;
}

.ImageView .card-img-top, .ImageView video {
  max-height: 800px;
  max-width: 1500px;
}

.ImageView .card-body {
  background-color: #222;
}

.ImageView .card-title {
  color: #f0f0f0;
}




.ImagePreview {
  margin: auto;
  width: 300px;
}

.ImagePreview .card-img-top {
  object-fit: none;
  object-position: center;
  width: 100%;
  height: 250px;
  background-color: #000;
}

.ImagePreview .card-body {
  background-color: #222;
}

.ImagePreview .card-title {
  color: #d0d0d0;
}

.ImagePreview .card-title:hover {
  color: #fff;
}

h3.GridTitle {
  color: #d0d0d0;
  background-color: #222;
}

a.GridTitle {
  color: #d0d0d0;
}

a.GridTitle:hover {
  color: #fff;
  text-decoration: none;
}

li.GridTitle {
  margin: 20px;
  margin-bottom: 0px;
  background-color: #222;
}

.Grid {
  margin: auto;
}

.GridElement .card {
  margin-top: 20px;
  width: 300px;
}

.GridElement:hover {
  color: #909090;
  text-decoration: none;
}

.FocusView {
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.FocusPagination {
  display: flex;
  flex-direction: row;
  justify-content: center;
}


.FocusPagination a.page-link {
  color: #222;
}

.FocusPagination .disabled a.page-link {
  color: #aaa;
}

.FocusPagination .disabled div.page-link {
  color: #aaa;
}

.Sidebar {
  height: 100%;
  width: 130px;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #111;
  overflow-x: hidden;
  padding-top: 20px;
}


.Sidebar .HideButton:hover {
  color: #fff;
}

.HideButton {
  /* padding: 6px 8px 6px 8px; */
  text-decoration: none;
  font-size: 20px;
  color: #d0d0d0;
  display: block;
  position: relative;
  bottom: 0%;
  text-align: center;
  width: 100%;
  cursor: pointer;
}

.SideLink {
  padding: 6px 8px 6px 8px;
  text-decoration: none;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  color: #d0d0d0;
  display: block;
}

.SideLink:hover {
  color: #fff;
  text-decoration: none;
}

.SideDivider {
  display: block;
  margin-top: 0em;
  margin-bottom: 0em;
  margin-left: 0.5em;
  margin-right: 0.5em;
  border-width: 1px;
  border-style: solid;
  color: #a0a0a0;
}

.BlogContainer {
  padding-top: 2em;
  background-color: #111;
  color: #d0d0d0;
  font-family: monospace;
  height: 100vh;
  padding-left: 2em;
  padding-right: 2em;
  overflow-y: scroll; }

.BlogPost {
  border-left: 0.1em solid #d0d0d0;
  padding-left: 1.5em;
  padding-right: 2em; }

.BlogPostTitle {
  text-align: left; }

.BlogPostDate {
  text-align: left; }

.BlogPostBody {
  text-align: left;
  color: #d0d0d0;
  overflow-x: auto; }

.PostDivider {
  display: block;
  margin-top: 0em;
  margin-bottom: 0em;
  margin-left: 8em;
  margin-right: 8em;
  border-width: 1px;
  border-style: solid;
  color: #a0a0a0; }

.ZineImgRow {
  position: relative;
  background-color: #111; }

.ZineImgContainer {
  padding-right: 34px;
  background-color: #111; }

.ArtistStatement {
  font-size: 20px;
  font-family: "Lucida Console", Monaco, monospace;
  background-color: #111;
  color: #d0d0d0;
  width: 80%;
  text-align: left;
  display: inline-block; }

.ArtistStatementPage {
  position: relative;
  padding-right: 34px;
  padding-top: 30px;
  background-color: #111;
  text-align: center;
  height: 100vh;
  width: 100%; }

.StatementLink {
  color: #d0d0d0;
  font-family: "Lucida Console", Monaco, monospace;
  font-size: 12px; }

.StatementLink:hover {
  color: #fff;
  text-decoration: none; }

.ZineBackLink {
  color: #d0d0d0;
  font-family: "Lucida Console", Monaco, monospace;
  font-size: 20px; }

.ZineBackLink:hover {
  color: #fff;
  text-decoration: none; }

.ArtProjectsTitle {
  font-family: Arial, Helvetica, sans-serif;
  color: #d0d0d0;
  font-size: 60px; }

.ArtProjectsIndex {
  background-color: #111;
  height: 100vh;
  overflow-y: scroll; }

.ProjectLinkContainer {
  color: #d0d0d0; }

.ProjectLink {
  text-decoration: none;
  font-size: 40px;
  font-family: Arial, Helvetica, sans-serif;
  color: #d0d0d0; }

.ProjectLink:hover {
  color: #fff;
  text-decoration: none; }

.ProjectDescription {
  color: #d0d0d0;
  overflow-x: auto; }

.App {
  text-align: center;
  margin-left: 130px;
  overflow-x: hidden;
  overflow-y: hidden;
}

.App::-webkit-scrollbar {
  display: none;
}

.ViewSwitcher {
  overflow-x: hidden;
  overflow-y: scroll;
  height: 100%;
}

.ViewSwitcher::-webkit-scrollbar {
  display: none;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 70px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

