.Sidebar {
  height: 100%;
  width: 130px;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #111;
  overflow-x: hidden;
  padding-top: 20px;
}


.Sidebar .HideButton:hover {
  color: #fff;
}

.HideButton {
  /* padding: 6px 8px 6px 8px; */
  text-decoration: none;
  font-size: 20px;
  color: #d0d0d0;
  display: block;
  position: relative;
  bottom: 0%;
  text-align: center;
  width: 100%;
  cursor: pointer;
}

.SideLink {
  padding: 6px 8px 6px 8px;
  text-decoration: none;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  color: #d0d0d0;
  display: block;
}

.SideLink:hover {
  color: #fff;
  text-decoration: none;
}

.SideDivider {
  display: block;
  margin-top: 0em;
  margin-bottom: 0em;
  margin-left: 0.5em;
  margin-right: 0.5em;
  border-width: 1px;
  border-style: solid;
  color: #a0a0a0;
}
